<template>
 <div class="fangGE"  v-on:click="y">
   <div v-if="bool"> {{t}} </div>
   <div v-else v-on:click="bool = true"> </div>
 </div>
</template>

<script>
export default{
    props:["n"],
  data(){
    return{
      bool:false,
      t:null
    }
  },
  methods:{
      y(){
        if(this.t !=null){
          return;
        }
        this.t  = this.n%2 > 0?"x":"o";
        console.log(this.n);
        
        this.$emit("click",this.t);
      }
 }
}
</script>

<style>
.fangGE div{
  width:40px;
  height: 40px;
  background-color: aqua;
  border: 1px solid #000;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
}
</style>
