<template>
 <div class="flex" >
   <div>第{{i}}手</div>
   <div class="row">
    <text01 @click="onclickcell(0,$event)" :n="i"/>
    <text01 @click="onclickcell(1,$event)" :n="i"/>
    <text01 @click="onclickcell(2,$event)" :n="i"/>
   </div>
   <div class="row">
    <text01 @click="onclickcell(3,$event)" :n="i"/>
    <text01 @click="onclickcell(4,$event)" :n="i"/>
    <text01 @click="onclickcell(5,$event)" :n="i"/>
   </div>
   <div class="row">
    <text01 @click="onclickcell(6,$event)" :n="i"/>
    <text01 @click="onclickcell(7,$event)" :n="i"/>
    <text01 @click="onclickcell(8,$event)" :n="i"/>
   </div>
  <div>{{isSheng}}</div>
 </div>
</template>

<script>
import text01 from "./text01";

export default{
  data(){
    return{
      quanju:0,
      i: 0,
      map:[
        [null,null,null],
        [null,null,null],
        [null,null,null]
      ],
     isSheng:"胜负未分"

    }
  },
 components:{ text01
  
  },
 methods:{
   onclickcell(t,text){
     if (this.isSheng != "胜负未分") {
       return;
     } 
     this.i++;
     var map = this.map;
    this.map[Math.floor(t/3)][t%3] = text;
    console.log("text:"+text);
    for (let i = 0; i < 3; i++) {

     if(map[i][1]==map[i][0]&&map[i][1]==map[i][2]&&map[i][1]!=null){

      this.isSheng = text+"胜利";
     }
     if(map[1][i]==map[2][i]&&map[2][i]==map[0][i]&&map[0][i]!=null){
      this .isSheng = text+"胜利";
     }

     
     if(map[0][0]==map[1][1]&&map[1][1]==map[2][2]&&map[1][1]!=null){
      this.isSheng = text+"胜利";
     }
      if(map[0][2]==map[1][1]&&map[1][1]==map[2][0]&&map[1][1]!=null){
      this.isSheng = text+"胜利";
     }
   
      
    }
    


   }
 }
}
</script>

